<template>
    <div class="tab">
        <div class="icons">
            <i :class="['icon-tuoyuankaobei', {'icon': step >= 1}]"></i>
            <div class="border"></div>
            <i :class="['icon-tuoyuankaobei', {'icon': step >= 2}]"></i>
            <div class="border"></div>
            <i :class="['icon-tuoyuankaobei', {'icon': step == 3}]"></i>
        </div>
        <div class="title-box">
            <p class="doing" :class="{'done': step >= 1}">实名认证</p>
            <p class="doing" :class="{'done': step >= 2}">绑定银行卡</p>
            <p class="doing" :class="{'done': step == 3}">完成</p>
        </div>
    </div>
</template>
<script>
export default {
    name: 'schedule',
    props: {
        step: {
            type: Number,
            default: 1
        }
    },
    data() {
        return {

        }
    }
}
</script>
<style lang="scss" scoped>
.tab {
    height: t(150);
    .icons {
        display: flex; // height: t(150);
        padding: 0 t(28) 0 t(28);
        padding-top: t(40);
        &>i {
            text-align: center;
            display: inline-block;
            font-size: t(30);
            color: #999999;
        }
        &>i:nth-child(1) {
            padding-left: t(39);
        }
        .border {
            height: t(14);
            border-bottom: 1px dashed #F28300;
        }
        .border:nth-of-type(1) {
            width: t(263);
        }
        .border:nth-of-type(2) {
            width: t(292);
        }
        .icon {
            color: #F28300;
        }
    }
    .title-box {
        display: flex;
        p {
            font-size: t(26);
        }
        p:nth-of-type(1) {
            margin-left: t(31);
        }
        p:nth-of-type(2) {
            margin: 0 t(230) 0 t(178);
        }
        .doing {
            color: #999999;
        }
        .done {
            color: #333333;
        }
    }
}
</style>




