<template>
    <v-page>
        <div slot="header">
            <v-header style="position:relative">
                <v-btn :icon="'icon icon-back'"></v-btn>
                <h1>实名认证</h1>
            </v-header>
        </div>
        <v-schedule :step="1"></v-schedule>
        <main>
            <p>证件照</p>
            <div class="photo-box">
                <img :src="frontData.frontimage" alt="">
            </div>
            <div class="photo-box">
                <img :src="reverseData.backimage" alt="">
            </div>
            <ul>
                <li>
                    <label>姓名</label>
                    <span>{{frontData.name}}
                        <a :class="'icon-bianji'"></a>
                    </span>
                </li>
                <li>
                    <label>身份证号</label>
                    <span>{{frontData.id}}</span>
                </li>
                <li>
                    <label>有效期</label>
                    <span>{{reverseData.valid_date}}</span>
                </li>
            </ul>
        </main>
        <v-button @click="go">确认并提交</v-button>
    </v-page>
</template>
<script>
    import {vHeader, vBtn} from '@base/header'
    import {vInput} from '@base/input'
    import {vSchedule} from '../component/schedule'
    export default {
        name: 'identification',
        components: {
            vHeader,
            vBtn,
            vInput,
            vSchedule,
        },
        data() {
            return {
                frontPhone: "",
                reversePhone: "",
                name: "",
                ID: "",
                validDate: "",
                // 身份证正面信息
                frontData: {
                    errorcode: 0,   //错误码，0代码成功并调用resolve，否则失败并调用reject
                    errormsg: '', //错误信息
                    card_type: 0, //0-正面，1-反面
                    name: '', //姓名
                    sex: '',    //性别
                    nation: '',   //民族
                    birth: '',  //出生日期
                    address: '', //地址
                    portrait: '', //头像
                    id: '', //身份证号
                    frontimage: '', //证件正面照 *暂无提供
                },
                // 身份证背面信息
                reverseData: {
                    errorcode: 0,   //错误码，0代码成功并调用resolve，否则失败并调用reject
                    errormsg: '', //错误信息
                    card_type: 1, //0-正面，1-反
                    backimage: '',  //证件反面照
                    valid_date: '', //有效期
                    authority: '', //发证机关
                }
            }
        },
        methods: {
            go() {
                T.go("main/result/index")
            },
            // 获取传过来的数据
            getPageParams () {
                window.T.Native.getPageParams().then((data) => {
                    this.frontData = data.params.frontData
                    this.reverseData = data.params.reverseData
                })
            }
        },

    }
</script>
<style lang="scss" scoped>
    .icon-scan {
        float: left;
    }

    h1 {
        position: absolute;
        left: t(304);
        right: t(305);
    }

    main {
        padding: t(45) 0 0 t(28);
        background: #ffffff;
        p {
            font-size: t(28);
        }
        .photo-box {
            display: inline-block;
            margin: t(39) 0 t(48) 0;
            img {
                width: t(317);
                height: t(204);
            }
        }
        .photo-box:nth-of-type(1) {
            margin: 0 t(18) 0 t(21);
        }
        ul {
            li {
                display: flex;
                box-sizing: border-box;
                font-size: t(32);
                height: t(100);
                line-height: t(100);
                border-top: 1px solid #EAEAEA;
                padding-right: t(27);
                span {
                    flex: 1;
                    text-align: right;
                    color: #999999;
                }
            }
        }
    }

    .ui-button {
        width: t(670);
        height: t(90);
        border-radius: t(45);
        margin: t(63) t(40) 0 t(40);
        background: linear-gradient(124.6deg, rgba(253, 58, 4, 1), rgba(242, 147, 0, 1));
        &:active {
            background: linear-gradient(124.6deg, rgba(221, 26, 0, 1), rgba(210, 115, 0, 1));
        }
    }
</style>





